<template>
	<div id="category-post">
		<public-header title="编辑分类"></public-header>
		<div class="content">
			<div class="padding-10-tb padding-15-lr font-14 c-gray">基本信息</div>
			<van-cell-group>
				<van-cell title="商品分类图片" class="no-fix flex">
					<div slot="right-icon" class="flex goods-thumb-wrap">
						<img class="goods-thumb " :src="thumb.url" alt="" v-if="thumb.url">
						<img class="goods-thumb " src="static/img/add_pic.png" alt="" v-else>
						<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
					</div>
				</van-cell>
				<van-field
					v-model="now_category.title"
					label="分类名称"
					placeholder="请输入分类名称"
				/>
				<van-field
					v-model="now_category.displayorder"
					label="分类排序"
					placeholder="请输入分类排序"
				/>
				<van-field
					v-model="now_category.min_fee"
					label="最低消费金额"
					placeholder="请输入最低消费金额"
				/>
			</van-cell-group>
			<template v-if="!now_category.id">
				<div class="padding-10-tb padding-15-lr font-14 c-gray">所属一级分类</div>
				<van-cell-group>
					<van-cell title="已有一级分类">
						<div slot="right-icon" class="flex" @click="onCategoryPicker()">
							<span>{{now_category.categorysTitle ? now_category.categorysTitle : '请选择'}}</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
				</van-cell-group>
			</template>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">分类描述</div>
			<van-cell-group>
				<van-field
					v-model="now_category.description"
					type="textarea"
					placeholder="请输入分类描述"
					rows="3"
				/>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">售卖时间</div>
			<van-radio-group v-model="now_category.is_showtime">
				<van-cell-group>
					<van-cell title="全时段售卖" clickable @click="now_category.is_showtime = '0'">
						<van-radio name="0" />
					</van-cell>
					<van-cell title="限时售卖" clickable @click="now_category.is_showtime = '1'">
						<van-radio name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<template v-if="now_category.is_showtime == 1">
				<div class="padding-10-tb padding-15-lr font-14 c-gray">售卖日</div>
				<div class="sail-day padding-10-lr padding-5-t padding-5-b bg-default">
					<div class="weekday" v-for="(item, index) in weeks">
						<div class="weekday-inner" :class="{'active': now_category.limit_week[index + 1] == index + 1}" @click="onSelectWeek(index + 1)">{{item}}</div>
					</div>
				</div>
				<div class="padding-10-tb padding-15-lr font-14 c-gray">售卖时段</div>
				<van-cell>
					<span @click="onTogglePicker('s')" v-if="now_category.start_time">{{now_category.start_time}}</span>
					<span @click="onTogglePicker('s')" v-else>0:00</span>
					<span class="padding-15-lr">至</span>
					<span @click="onTogglePicker('e')" v-if="now_category.end_time">{{now_category.end_time}}</span>
					<span @click="onTogglePicker('e')" v-else>0:00</span>
				</van-cell>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info font-16" @click="onSubmit">保存</van-button>
				<van-button size="normal" block class="bg-danger font-16 margin-15-t" @click="onDeleteCategory(now_category)" v-if="id > 0">删除</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--时间选择器-->
		<van-popup v-model="timePicker" position="bottom">
			<div class="popup-content">
				<van-datetime-picker
					v-model="currentDate"
					type="time"
					:min-hour="0"
					:max-hour="23"
					@confirm="onConfirmTime"
					@cancel="onTogglePicker"
				/>
			</div>
		</van-popup>
		<!--分类选择器-->
		<van-popup v-model="categoryPicker" position="bottom">
			<div class="popup-content">
				<van-picker
					show-toolbar
					value-key="title"
					:columns="categorys"
					@cancel="onCategoryPicker"
					@confirm="onConfirmCategory"
				/>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				id: 0,
				weeks: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
				timePicker: false,
				currentDate: '',
				categoryPicker: false,
				categorys: {},
				thumb: {},
				now_category: {
					title: '',
					displayorder: '',
					min_fee: '',
					description: '',
					is_showtime: 0,
					limit_week: {},
					start_time: '0:00',
					end_time: '0:00',
					categorysTitle: '',
					thumb: ''
				},
				TimeType: 's',
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					url: 'manage/goods/category/post',
					data: {
						id: that.id
					},
					success: (res) => {
						that.now_category = that.util.extend(that.now_category, res.now_category);
						that.categorys = res.categorys;
						if(that.id > 0) {
							that.thumb.url = that.now_category.thumb;
						}
					}
				});
			},
			onSelectWeek(value) {
				var that = this;
				if (that.now_category.limit_week[value]){
					this.$delete(that.now_category.limit_week, value);
				} else {
					this.$set(that.now_category.limit_week, value, value);
				}
			},
			onTogglePicker(type) {
				this.timePicker = !this.timePicker;
				if (type == 's' || type == 'e'){
					this.TimeType = type;
				}
			},
			onConfirmTime(value){
				if (value){
					if(this.TimeType == 's'){
						this.now_category.start_time = value;
					} else {
						this.now_category.end_time = value;
					}
				}
				this.onTogglePicker();
			},
			onUploadImage(e) {
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.thumb = {
								url: result.url,
								filename: result.filename,
							};
							that.thumb = Object.assign({}, that.thumb);
							that.now_category.thumb = that.thumb.filename;
						}
					},
					options: {channel: 'h5'}
				});
			},
			onSubmit(){
				var that = this;
				if (!that.now_category.title) {
					that.util.$toast('请输入分类名称', '', 1000);
					return false;
				}
				if (that.now_category.is_showtime == 1){
					if (!that.now_category.start_time){
						that.util.$toast('请选择限购开始时间', '', 1000);
						return false;
					}
					if (!that.now_category.end_time){
						that.util.$toast('请选择限购结束时间', '', 1000);
						return false;
					}
				}
				setData({
					vue: this,
					url: 'manage/goods/category/post',
					data: {
						id: that.id,
						params: JSON.stringify(that.now_category)
					},
					redirect: that.util.getUrl({path: '/pages/category/index'}),
					redirectType: 'replace'
				});
			},
			onDeleteCategory(now_category){
				var that = this;
				setData({
					vue: this,
					url: 'manage/goods/category/del',
					data: {
						id: now_category.id,
					},
					confirm: '确定删除此分类吗',
					redirect: that.util.getUrl({path: '/pages/category/index'}),
					redirectType: 'replace',
					message: '删除分组成功'
				});
			},
			onCategoryPicker(){
				this.categoryPicker = !this.categoryPicker;
			},
			onConfirmCategory(picker){
				this.now_category.parentid = picker.id;
				this.now_category.categorysTitle = picker.title;
				this.onCategoryPicker();
			},
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#category-post .sail-day{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
	}
	#category-post .sail-day .weekday{
		width: 20%;
		font-size: 12px;
		padding: 5px;
	}
	#category-post .sail-day .weekday .weekday-inner{
		width: 100%;
		text-align: center;
		padding: 3px 0;
		border-radius: 1px;
		border: 1px solid #ccc;
		position: relative;
	}
	#category-post .sail-day .weekday .weekday-inner.active{
		background: url("../../../static/img/selected-success.png") no-repeat bottom right;
		background-size: 15px auto;
		color: #33aafc;
		border-color: #33aafc;
	}
	#category-post .goods-thumb-wrap{
		position: relative;
	}
	#category-post .weui-uploader__input{
		display: block;
		width: 60px;
		height: 60px;
		opacity: 0;
		position: absolute;
		top: 0px;
		left: 30px;
	}
	#category-post .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 3px;
		margin-left: 30px;
	}
</style>


